import { Routes, RouterModule } from '@angular/router';

import { RegisterComponent } from './register/register.component';
import { LoginComponent } from './login/login.component';
import { UploadComponent } from './upload/upload.component';
import { ViewUploadsComponent} from './view-uploads/view-uploads.component';
import { AccountApproveComponent } from './account-approve/account-approve.component';
import { EmailConfirmationComponent} from './email-confirmation/email-confirmation.component';

import { AuthGuard } from './helpers/authguard';
const appRoutes: Routes = [
    {path: 'user/confirmation/:id/:token', component: EmailConfirmationComponent},
    { path: 'register', component: RegisterComponent },
    { path: 'approve', component: AccountApproveComponent },
    { path: 'login', component: LoginComponent },
    { path: 'upload', component: UploadComponent},
    { path: 'view', component: ViewUploadsComponent, canActivate: [AuthGuard]},
    // otherwise redirect to home
    { path: '**', redirectTo: 'view' }
];
export const routing = RouterModule.forRoot(appRoutes);



