import { Component, OnInit } from '@angular/core';
import { DataService } from '../data.service';
import { RouterLink, Router, ActivatedRoute } from '@angular/router';

interface Xdata {
  status?: Number;
  message?: string;
  data?: any;
}

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {
alert = '';
alertClass = 'alert-danger';
  constructor(private dataService: DataService, private router: Router, private aRoute: ActivatedRoute) {
    if (localStorage.getItem('currentUser')) {
      this.router.navigate(['view']);
    }
    this.alert = '';
    aRoute.queryParams.subscribe(params => {
      if (params['landStatus'] == 'session_expired') {
        this.alertClass = 'alert-danger';
        this.alert = 'Your session has expired. Please log in to continue';
      } else if (params['landStatus'] === 'ACCOUNT_ACTIVATED') {
        this.alertClass = 'alert-success';
        this.alert = 'Your account is activated. Please log in to continue';
      }
    });
  }
  cred: any = {};

  ngOnInit() {
  }

  login() {
    this.alert = '';
    this.dataService.login(this.cred)
        .subscribe(
            api_data => {
              const data: Xdata = api_data;
              if (data.status === 200) {
                localStorage.setItem('currentUser', JSON.stringify(data.data));
              this.router.navigate(['view']);
              } else {
                if (data.message === 'USER_NOT_ACTIVE' ) {
                  this.alertClass = 'alert-danger';
                  this.alert = 'Your account is not active. Please check your mail for confirmation email';
                } else {
                  this.alertClass = 'alert-danger';
                  this.alert = data.message;
                }
              }
            },
            error => {
                console.log(error);
            });
  }
}





