import { Component, OnInit } from '@angular/core';
import { DataService } from '../data.service';
import {  FileUploader, ParsedResponseHeaders, FileItem } from 'ng2-file-upload';
import { Router } from '@angular/router';
import { RouterLink } from '@angular/router';

@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.css']
})
export class RegisterComponent implements OnInit {
  model: any = {};
  visibleTab: Number = 1;
  private uploading = false;
  constructor(private dataService: DataService, private router: Router) { }

  public uploader: FileUploader = new FileUploader({
    url: './api/user/register', itemAlias: 'identityCard',
    parametersBeforeFiles: true
  });
  ngOnInit() {
    this.uploader.onAfterAddingFile = (file) => {
      file.withCredentials = false;
    };
    // overide the onCompleteItem property of the uploader so we are
    // able to deal with the server response.
    this.uploader.onProgressItem = (item: FileItem, progress: any) => {
      this.uploading = true;
    };
    this.uploader.onCompleteItem = (item: any, response: any, status: any, headers: any) => {
      this.uploading = false;
      let data = JSON.parse(response);
         if ( data.status == 200 && data.message == 'SUCCESS') {
           this.visibleTab = 200;
         }
     };
     this.uploader.onBuildItemForm = (item, form) => {
       form.append('name', this.model.name);
       form.append('card', this.model.card);
       form.append('sno', this.model.sno);
       form.append('tpno', this.model.tpno);
       form.append('email', this.model.email);
       form.append('password', this.model.password);
      };
  }
  register() {
    console.log(this.model);
    /*this.dataService.create(this.model)
        .subscribe(
            data => {
                // set success message and pass true paramater to persist the message after redirecting to the login page
              console.log('Registration successful');
              this.router.navigate(['login']);
            },
            error => {
                console.log(error);
            });*/
  this.visibleTab = 2;
  }
}



