import { BrowserModule } from '@angular/platform-browser';
import { NgModule, NO_ERRORS_SCHEMA } from '@angular/core';
import { HttpClientModule, HTTP_INTERCEPTORS } from '@angular/common/http';
import { FormsModule } from '@angular/forms';
import { MDBBootstrapModule } from 'angular-bootstrap-md';
import { FileUploadModule } from 'ng2-file-upload/ng2-file-upload';
import { NgbModule } from '@ng-bootstrap/ng-bootstrap';

import { routing } from './app.routing';
import { AppComponent } from './app.component';
import { DataService } from './data.service';
import { RegisterComponent } from './register/register.component';
import { LoginComponent } from './login/login.component';
import { UploadComponent } from './upload/upload.component';
import { directive } from '@angular/core/src/render3/instructions';
import { ViewUploadsComponent } from './view-uploads/view-uploads.component';
import { JwtInterceptor } from './helpers/jwt.interceptor';
import { AuthGuard } from './helpers/authguard';
import { NavbarComponent } from './navbar/navbar.component';
import { AccountApproveComponent} from './account-approve/account-approve.component';
import { EmailConfirmationComponent } from './email-confirmation/email-confirmation.component';

@NgModule({
  declarations: [
    AppComponent,
    RegisterComponent,
    LoginComponent,
    UploadComponent,
    ViewUploadsComponent,
    NavbarComponent,
    AccountApproveComponent,
    EmailConfirmationComponent
  ],
  imports: [
    MDBBootstrapModule.forRoot(),
    BrowserModule,
    routing,
    FormsModule,
    HttpClientModule,
    FileUploadModule
  ],
  providers: [
    DataService,
    AuthGuard,
    {
      provide: HTTP_INTERCEPTORS,
      useClass: JwtInterceptor,
      multi: true
    }
  ],
  schemas: [ NO_ERRORS_SCHEMA ],
  bootstrap: [AppComponent]
})
export class AppModule { }



