import { Component, OnInit, ViewChild } from '@angular/core';
import { DataService } from '../data.service';
import { NgModule } from '@angular/core';
import { ModalDirective } from 'angular-bootstrap-md';


// --------------------------------------- END OF CONTENT CLASS ----------------------------------//

interface Xdata {
  status?: Number;
  data?: any;
}

@Component({
  selector: 'app-account-approve',
  templateUrl: './account-approve.component.html',
  styleUrls: ['./account-approve.component.scss']
})

export class AccountApproveComponent implements OnInit {
  accounts: any;
  imageToShow: any;
  isImageLoading: any;

  constructor(private dataService: DataService) {
    this.dataService.getPendingAccounts().subscribe(api_data => {
      const d: Xdata = api_data;
      this.accounts = d.data.profile;
    });
  }

  // -----------------------

  @ViewChild('myModal') public autoShownModal: ModalDirective;
  public isModalShown = false;

  public showModal(filename): void {
    this.isModalShown = true;
    var path = this.getImageUrl(filename);
    this.getImageFromService(path);
  }

  public hideModal(): void {
    this.autoShownModal.hide();
  }

  public onHidden(): void {
    this.isModalShown = false;
  }

  createImageFromBlob(image: Blob) {
    let reader = new FileReader();
    reader.addEventListener('load', () => {
        this.imageToShow = reader.result;
    }, false);

    if (image) {
        reader.readAsDataURL(image);
    }
}
getImageFromService(url: string) {
    this.isImageLoading = true;
    this.dataService.getImage(url).subscribe(data => {
        this.createImageFromBlob(data);
        this.isImageLoading = false;

    }, error => {
        this.isImageLoading = false;
        // console.log(error);
    });
}
  getImageUrl(name: any) {
    return '/api/user/id?fn=' + name;
  }

  approveAccount(sno: string, id: string) {
    this.dataService.approve(sno, id).subscribe(data => {
      console.log(data);
    });
  }
  ngOnInit() {
  }

}



