import { Component, OnInit } from '@angular/core';
import {  FileUploader } from 'ng2-file-upload';
import { Router } from '@angular/router';
import { DataService } from '../data.service';

const URL = './api/upload';

@Component({
  selector: 'app-upload',
  templateUrl: './upload.component.html',
  styleUrls: ['./upload.component.css']
})
export class UploadComponent implements OnInit {
subjects: any = {};
  model: any = {};
  constructor(private router: Router, private dataService: DataService) {
    this.dataService.getSubjects().subscribe(data => {this.subjects = data; console.log(this.subjects); });
  }
    // declare a property called fileuploader and assign it to an instance of a new fileUploader.
    // pass in the Url to be uploaded to, and pass the itemAlais, which would be the name of the //file input when sending the post request.
    private currentUser = JSON.parse(localStorage.getItem('currentUser'));
    public uploader: FileUploader = new FileUploader({
      url: URL, itemAlias: 'file',
      headers: [{name: 'Authorization', value: `${this.currentUser.token}`}],
      parametersBeforeFiles: true,
    });

    ngOnInit() {
    // override the onAfterAddingfile property of the uploader so it doesn't authenticate with //credentials.
    this.uploader.onAfterAddingFile = (file) => { file.withCredentials = false;
    };
    // overide the onCompleteItem property of the uploader so we are
    // able to deal with the server response.
    this.uploader.onCompleteItem = (item: any, response: any, status: any, headers: any) => {
         console.log('ImageUpload:uploaded:'  + status);
     };
     this.uploader.onBuildItemForm = (item, form) => {
       form.append('title', this.model.title);
       form.append('desc', this.model.desc);
       form.append('course', this.model.course);
       form.append('type', 'file');
    };
 }
 register() {
     this.router.navigate(['register']);
 }
}



