import { Component, OnInit } from '@angular/core';
import { DataService } from '../data.service';
import { ActivatedRoute } from '@angular/router';
import { RouterLink } from '@angular/router';

interface Xdata {
  status?: Number;
  data?: any;
}

@Component({
  selector: 'app-view-uploads',
  templateUrl: './view-uploads.component.html',
  styleUrls: ['./view-uploads.component.css']
})

export class ViewUploadsComponent implements OnInit {
  files: any;
  constructor(private dataService: DataService) {
    this.dataService.getUploads().subscribe(api_data => {
      const data: Xdata = api_data;
      if (!(data.status === 500)) {
        this.files = data.data;
      } else {
        alert(data.status);
      }

     });
  }
  download(content: any) {
      let link = document.createElement('a');
      link.href = './api/file/' + content.fileId;
      link.download = content.fileName;
      link.click();
  }
  ngOnInit() {
  }

}



